'use client';
import Image from "next/image";

type Props = {
    avatarUrl: string;
    name: string;
    createAt: string;
};

const SenderInfo = ({ avatarUrl, name, createAt }: Props) => {
    const handleDownload = () => {
        const url = '/CP3.pdf'; // ƒê∆∞·ªùng d·∫´n file
        const link = document.createElement('a');
        link.href = url;
        link.download = 'CP3.pdf';
        link.click();
    };
    return (
        <div className="flex justify-between items-center p-4 border-b border-gray-200">
            {/* Th√¥ng tin ng∆∞·ªùi g·ª≠i */}
            <div className="flex items-center space-x-4">
                {/* H√¨nh ƒë·∫°i di·ªán */}
                <div className="w-10 h-10 relative flex-shrink-0">
                    <Image
                        src={avatarUrl}
                        alt={name}
                        layout="fill"
                        objectFit="cover"
                        className="rounded-full"
                    />
                </div>

                <div>
                    <p className="font-semibold text-md">{name}</p>
                    <p className="text-gray-500 text-sm">{createAt.toLocaleString()}</p>
                </div>
            </div>

            {/* N√∫t t·∫£i xu·ªëng */}
            <button
                onClick={handleDownload}
                className="bg-blue-500 text-white px-4 py-2 rounded-md shadow hover:bg-blue-600 transition duration-300"
            >
                T·∫£i xu·ªëng
            </button>
        </div>
    );
}
export default SenderInfo;
